

import React, {useRef, useState} from 'react';
// import Box from '@mui/material/Box';
import {BrowserRouter, Navigate, Route, Routes} from 'react-router-dom';
import Login from './Login';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import Mainlist from './Mainlist';
import CreateAccount from './CreateAccount';
import {ProtectedRoute} from './ProtectedRoute';
import {AuthProvider, useAuth} from './AuthProvider';
import {useLocalStorage} from './UseLocalStorage';
import Rightbar from './Rightbar';

/**
 * Simple component with no state.
 *
 * @return {object} JSX
 */

 const dataBaseContext = React.createContext();

function App() {
  const authentication = useAuth();
  const [dataChanged, setDataChanged] = React.useState(false);
  const data = useLocalStorage('user', null);
  let user = false;

  useRef(() => {
    authentication.setUser(data);
    user = authentication.user;
  });

  return (
    <BrowserRouter>
      <AuthProvider>
        <dataBaseContext.Provider value={{dataChanged, setDataChanged}}>
          <Routes>
            <Route path='/createaccount' element={<CreateAccount/>} />
            <Route path='/' element=
              {user ? <Navigate to='/mainlist'/> : <Login/>}/>
            <Route element={<ProtectedRoute/>}>
              <Route path='/mainlist' element={<div>
                <Mainlist/><Navbar/><Sidebar/><Rightbar/>
              </div>} />
            </Route>
          </Routes>
        </dataBaseContext.Provider>
      </AuthProvider>
    </BrowserRouter>
  );
}

export default App;
export {
  dataBaseContext,
};